.banner {
    height: 50vh;
    position: relative;
    background:  url("https://cdn-image.travelandleisure.com/sites/default/files/styles/1600x1000/public/1507145267/georgetown-maine-airbnb-rental-BNBTREETOP1017.jpg?itok=GFHqrrq_") center center;
}

.banner__info {
    background-color: #000;
    color: #fff;
    padding-top: 25vh;
    padding-left: 50px;
    padding-right: 50px;
    padding-bottom: 40px;
    width: 400px;
}

.banner__searchButton {
    background-color: #fff!important;
    font-weight: 900!important;
    text-transform: inherit!important;
    color: #ff7779 !important;
}

.banner__search {
    display: flex;
    flex-direction: column;
}

.banner__info > button {
    background-color: #ff7779;
    color: #fff;
    text-transform: inherit;
    margin-top: 15px;
    font-weight: 600;
}

.banner__info > h5 {
    margin-top: 10px;
}

.banner__info > button:hover {
    background-color: #fff;
    color: #ff7779;
}