.search {
    position: absolute;
    top: 34.7px;
    left: 25%;
    width: 100vw;
}

.search > h2 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 559px;
    padding: 10px;
    background-color: #fff;
    position: absolute;
    left: 0;
    top: 330px;
}

.search > input {
    width: 559px;
    padding: 20px;
    left: 0;
    height: 30px;
    top: 420px;
    border: none;
}

.search > input:focus {
    outline-width: 0;
}

.search > button {
    position: absolute;
    left: 0;
    top: 415px;
    text-transform: inherit!important;
    background-color: #ff7779;
    color: #fff;
    width: 560px;
}

.search >button:hover {
    background-color: #fff;
    color: #ff7779;
}