.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    background-color: #fff;
    width: 100%;
    top: 0;
    z-index: 1000;

}


.header__icon {
    object-fit: contain;
    height: 100px;
    margin-left: 20px;
}

.header__center {
    display: flex;
    flex: 1;
    align-items: center;
    max-width: fit-content;
    padding: 18px;
    height: 30px;
    border: 1px solid lightgrey;
    border-radius: 10px;
}

.header__center > input {
    border: none;
    padding: 10px;
    outline-width: 0;
}

.header__right {
    display: flex;
    align-items: center;
    margin-right: 20px;
    justify-content: space-between;
    width: 15vw;
    margin-right: 80px;
}